/**
* Mote Customization
* override Mote style for locally installed mote
*
*/
html, body {
  background-color: #fff;
  margin: 0 auto;
  color: #000;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif; }

a {
  color: #000; }

h3, h4, h5 {
  line-height: 1.5; }

h5 {
  font-family: "Roboto", Helvetica, Arial, sans-serif; }

body > header {
  background-color: #242526; }
  body > header .logo {
    color: #fff;
    text-decoration: none; }
    body > header .logo img {
      margin: 0 0 15px; }
    body > header .logo small {
      color: #A6ACAF;
      padding: 0 0 0 15px; }

header {
  height: 117px;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 999999999999; }

.hero {
  background-color: #fff;
  padding: 50px 0 35px;
  border-bottom: 1px solid #D7DBDD;
  margin: 115px 0 0;
  height: auto; }
  .hero h1 {
    color: #000; }
  .hero *, .hero p {
    color: #5A5D5E; }

main {
  margin-top: 35px; }

@media screen and (min-width: 650px) {
  .content__prk {
    padding-right: 15%; } }

.content__prk .content:first-child {
  padding-top: 0; }

.content__prk .content {
  padding-top: 117px; }

@media screen and (min-width: 650px) {
  .content__heading-medium {
    padding-right: 40%; } }

@media screen and (min-width: 650px) {
  .content__heading-small {
    padding-right: 30%; } }

.content__description {
  color: #5A5D5E;
  font-weight: 300;
  font-size: 18px; }

.content__princinples {
  padding-top: 30px; }

#pdf-embed-iframe {
  width: 100%;
  height: 600px;
  margin-bottom: 10%; }
  #pdf-embed-iframe html, #pdf-embed-iframe body {
    background-color: #fff !important; }

.iframe-variation-toggler {
  border: 1px solid #e6e6e6;
  height: 30px;
  margin-bottom: 10px;
  text-transform: capitalize; }

.stickySidebar {
  position: fixed;
  top: 175px;
  overflow-x: hidden;
  overflow-y: scroll;
  bottom: 0;
  margin-bottom: 30px; }

.noStickySidebar {
  position: inherit; }

.unordered-list {
  padding-right: 10px;
  padding-bottom: 20px; }
  .unordered-list .unordered-list__item {
    padding: 5px 0; }

.ordered-list {
  list-style: decimal;
  padding-bottom: 20px; }
  .ordered-list .ordered-list__item {
    padding: 5px 0; }

[role="menu"] {
  padding: 0 0 0 10px; }

[role="menu"].vertical.tier-1 > [role="menuitem"] a {
  font-weight: bold; }

[role=menu].vertical.tier-2 > [role=menuitem] a {
  font-weight: normal; }

[role="menu"] [role="menuitem"] a {
  color: #000;
  text-transform: capitalize; }

[role="menu"] [role="menuitem"] a + [role="menu"] [role="menuitem"]:after {
  background-color: rgba(0, 0, 0, 0.8); }

[role="menu"] [role="menuitem"] a:hover,
[role="menu"] [role="menuitem"] a:focus,
[role="menu"] [role="menuitem"] a:visited:hover,
[role="menu"] [role="menuitem"] a:visited:focus,
[role="menu"] [role="menuitem"] a:active,
[role="menu"] [role="menuitem"] a.active {
  color: rgba(0, 0, 0, 0.5); }

[role="menu"] [role="menuitem"] a:active,
[role="menu"] [role="menuitem"] a.active {
  font-weight: bold; }

.extra-list__menu {
  padding: 10px 0 10px 20px;
  display: block;
  list-style: none;
  margin: 0; }

.extra-list__menuitem a {
  color: #000;
  text-decoration: none;
  padding: 5px 0; }
  .extra-list__menuitem a:hover, .extra-list__menuitem a:focus {
    color: rgba(0, 0, 0, 0.5); }

.extra-list__menuitem .is-active {
  color: rgba(0, 0, 0, 0.5);
  text-decoration: underline;
  font-weight: bold; }

.menuitem__main-project {
  color: #000;
  text-decoration: underline;
  font-weight: bold; }

.pattern__intro-heading {
  margin: 0; }

.pattern__intro-desc {
  margin-top: 20px;
  font-weight: normal; }

.sg-wrapper {
  margin-top: 35px; }

@media screen and (min-device-width: 320px) {
  .iframe-variation-toggler--label {
    display: block; }
  .iframe-variation-toggler {
    background: #3b8ec2 url(http://i62.tinypic.com/15xvbd5.png) no-repeat 96% 0;
    border: 1px solid #3b8ec2;
    padding: 5px 5px;
    border: none;
    color: white;
    border-radius: 5px;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    height: auto !important;
    width: 25%; } }

.devices-list {
  width: 100%;
  padding-top: 20px; }
  .devices-list tr:nth-child(even) {
    background: #f1f1f1; }
  .devices-list th {
    text-align: left;
    padding: 10px; }
  .devices-list td {
    padding: 10px; }
